import React, { useState, useEffect } from 'react';
import {
  Container,
  Paper,
  Typography,
  Button,
  TextField,
  Box,
  Grid,
  Alert,
  Chip,
  Avatar,
  Divider,
} from '@mui/material';
import { useNavigate } from 'react-router-dom';
import { getBalance, earnPoints, redeemPoints } from '../services/api';
import { getUserInfo } from '../services/api';
import AccountCircleIcon from '@mui/icons-material/AccountCircle';

const Dashboard: React.FC = () => {
  const navigate = useNavigate();
  const [balance, setBalance] = useState<number>(0);
  const [points, setPoints] = useState<string>('');
  const [error, setError] = useState<string>('');
  const [success, setSuccess] = useState<string>('');
  const [userEmail, setUserEmail] = useState<string>('');

  useEffect(() => {
    // Get user info
    const userInfo = getUserInfo();
    if (userInfo) {
      setUserEmail(userInfo.email);
    }

    // Fetch balance
    const fetchBalance = async () => {
      try {
        console.log('Fetching balance...');
        const response = await getBalance();
        console.log('Balance response:', response);
        setBalance(response.balance);
      } catch (err) {
        console.error('Error fetching balance:', err);
        setError('Failed to fetch balance');
      }
    };

    fetchBalance();
  }, []);

  const handleEarnPoints = async () => {
    try {
      const pointsNum = parseInt(points);
      if (isNaN(pointsNum) || pointsNum <= 0) {
        setError('Please enter a valid number of points');
        return;
      }

      await earnPoints(pointsNum);
      const response = await getBalance();
      setBalance(response.balance);
      setSuccess('Points earned successfully!');
      setPoints('');
      setError('');
    } catch (err) {
      setError('Failed to earn points');
      setSuccess('');
    }
  };

  const handleRedeemPoints = async () => {
    try {
      const pointsNum = parseInt(points);
      if (isNaN(pointsNum) || pointsNum <= 0) {
        setError('Please enter a valid number of points');
        return;
      }

      if (pointsNum > balance) {
        setError('Not enough points to redeem');
        return;
      }

      await redeemPoints(pointsNum);
      const response = await getBalance();
      setBalance(response.balance);
      setSuccess('Points redeemed successfully!');
      setPoints('');
      setError('');
    } catch (err) {
      setError('Failed to redeem points');
      setSuccess('');
    }
  };

  const handleLogout = () => {
    localStorage.removeItem('token');
    localStorage.removeItem('user_email');
    navigate('/login');
  };

  return (
    <Container maxWidth="md">
      <Box sx={{ mt: 4 }}>
        <Paper sx={{ p: 4 }}>
          <Box sx={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center', mb: 3 }}>
            <Typography variant="h4">
              Loyalty Program
            </Typography>
            <Chip
              avatar={<Avatar><AccountCircleIcon /></Avatar>}
              label={userEmail}
              variant="outlined"
              color="primary"
            />
          </Box>

          <Divider sx={{ mb: 3 }} />

          <Box sx={{ 
            mb: 4, 
            p: 3, 
            bgcolor: '#f5f9ff', 
            borderRadius: 2,
            display: 'flex',
            flexDirection: 'column',
            alignItems: 'center' 
          }}>
            <Typography variant="h6" color="textSecondary" gutterBottom>
              Current Balance
            </Typography>
            <Typography variant="h3" sx={{ fontWeight: 'bold', color: '#2196f3' }}>
              {balance} points
            </Typography>
          </Box>

          {error && (
            <Alert severity="error" sx={{ mb: 2 }}>
              {error}
            </Alert>
          )}

          {success && (
            <Alert severity="success" sx={{ mb: 2 }}>
              {success}
            </Alert>
          )}

          <Grid container spacing={2} alignItems="center">
            <Grid item xs={12} sm={6}>
              <TextField
                fullWidth
                label="Points"
                type="number"
                value={points}
                onChange={(e) => setPoints(e.target.value)}
                margin="normal"
              />
            </Grid>
            <Grid item xs={12} sm={6}>
              <Box sx={{ display: 'flex', gap: 2 }}>
                <Button
                  variant="contained"
                  color="primary"
                  onClick={handleEarnPoints}
                  fullWidth
                >
                  Earn Points
                </Button>
                <Button
                  variant="contained"
                  color="secondary"
                  onClick={handleRedeemPoints}
                  fullWidth
                >
                  Redeem Points
                </Button>
              </Box>
            </Grid>
          </Grid>

          <Box sx={{ mt: 4, display: 'flex', gap: 2, flexDirection: 'column' }}>
            <Button
              variant="outlined"
              color="primary"
              onClick={() => navigate('/history')}
              fullWidth
            >
              View Transaction History
            </Button>
            
            <Button
              variant="outlined"
              color="error"
              onClick={handleLogout}
              fullWidth
            >
              Logout
            </Button>
          </Box>
        </Paper>
      </Box>
    </Container>
  );
};

export default Dashboard; 